import React from 'react'
import styles from './BrewCardList.module.scss';
import BrewCard from '../BrewCard/BrewCard';


const BrewCardList = (props) => {
    const { beers } = props;

    const getBrewCardJsx = (beer) => (
       
        <div className={styles.card} key={beer.name}>
          <BrewCard beer={beer} />
        </div>
    );

    return (
        <section className={styles.CardList}>
        
        {beers.map(getBrewCardJsx)}
       
        </section>
    )
}

export default BrewCardList
