import React from 'react'
import styles from "./BrewCardFront.module.scss";

const BrewCardFront = (props) => {
    const {name,
        id,
        tagline,
        image_url,
     } = props.beer;
   
     return (
          <section className={styles.brewCardFront}>
            <div className={styles.beerDetails}>
                    <h2>{name}</h2>
                    <h4>ID: {id}</h4>
                  <p>{tagline}</p>
                  <div className={styles.imageBox}>
                    <img className={styles.image} src={image_url} alt="beer pic"/>
                  </div>
             </div>
          </section>
     );
    };
    


export default BrewCardFront
