
import { useState, useEffect } from 'react';
import styles from "./App.module.scss";
import NavBar from "./componants/NavBar/NavBar";
import Display from "./componants/Display/Display";


const App =() => {
  
const [beers, setBeers] = useState([]);
const [searchText,setSearchText] = useState("");

const fetchBeers = () => {
  return fetch("https://api.punkapi.com/v2/beers?page=1&per_page=75")
  .then(response => response.json())
  .then(data => data.map(beer => beer))
}

const searchBeers = (searchText) => {
  if (searchText !== "") {
    return fetch(`https://api.punkapi.com/v2/beers?beer_name=${searchText}`)
    .then(response => response.json())
    .then(result => {
      const beers = result.map(beers=>beers);
      setBeers(beers);
    })
  } else {
    fetchBeers().then(beers =>setBeers(beers));
  }
  
}


useEffect( () => {
  fetchBeers().then(beers =>setBeers(beers));

}, [])


  return (
    <section className={styles.appBack}>
      
      <NavBar setSearchText={setSearchText} setBeers={setBeers} searchBeers={searchBeers} totalList={fetchBeers} />
      <h1 className={styles.title}>BrewDog API HOME PAGE</h1>
      {beers && <Display beers={beers}/>}
      
    </section>
  );
}

export default App;
