import React, { useState } from "react";
import styles from './BrewCard.module.scss';
import BrewCardFront from "../BrewCardFront";
import BrewCardBack from "../BrewCardBack";

const BrewCard = (props) => {
  const { beer } = props;
  
  const [isFaceDown, setIsFaceDown] = useState(false);

  const flipStyles = isFaceDown ? styles.faceDown : "";

  return (
    <section
      className={`${styles.beerCard} ${flipStyles}`}
      onClick={() => setIsFaceDown(!isFaceDown)}
    >
      <div className={styles.front}>
        <BrewCardFront beer={beer} />
      </div>
      <div className={styles.back}>
        <BrewCardBack beer={beer} />
      </div>
    </section>
  );
}; 
  

export default BrewCard
